export default "// Specialized loops:\r\n\r\n// for..in loops over keys\r\nlet obj1 = { a : 1, b : 2, c : 3};\r\n\r\nfor(let key in obj1) {\r\n  console.log(\"Key: \" + key)\r\n}\r\n\r\n// for..of loops over iterable values\r\nlet valuesArray = [\"a\", \"b\", \"c\"];\r\n\r\nfor(let value of valuesArray) {\r\n  console.log(\"Value: \" + value);\r\n}"