export default "class User {\r\n    constructor(name) {\r\n      // Not actually \"private\" - just convention\r\n      this._name = name;\r\n    }\r\n  \r\n    // Can create getters and setters - not widely used\r\n    get name() {\r\n      return this._name;\r\n    }\r\n  \r\n    set name(value) {\r\n      if (value.length < 4) {\r\n        console.log(\"Name is too short.\");\r\n        return;\r\n      }\r\n      this._name = value;\r\n    }  \r\n\r\n    static staticMethod() {\r\n        console.log(\"This is the class definition\", this)\r\n    }\r\n}"