export default "// Larger example:\r\nlet xhr2 = new XMLHttpRequest();\r\nxhr2.open('GET', '/article/xmlhttprequest/example/load');\r\nxhr.setRequestHeader('Content-Type', 'application/json');\r\n\r\n// This will be called after the response is received\r\nxhr2.onload = function() {\r\n    if (xhr.status === 200) {  // could handle errors if not 200\r\n        // responseText is the server response contents\r\n        console.log(`Done, got ${xhr2.response.length} bytes`); \r\n    }\r\n};\r\nxhr2.send();\r\n\r\n// Other options:\r\nxhr.abort(); // terminate the request\r\nxhr.timeout = 10000; // timeout in ms, 10 seconds\r\n\r\n// Assume JSON response is: {\"message\": \"Hello, world!\"}\r\nxhr.responseType = 'json'; // auto-parse JSON response\r\nxhr.onload = function() {\r\n    let {response} = xhr; // properly parsed JS object\r\n    console.log(response.message); // \"Hello, world!\"\r\n};"