export default "// Basic syntax - really just sugar for \"prototypes\"\r\nclass MyClass {\r\n    constructor() {\r\n        super(); // must always call first if constructor used\r\n    }\r\n\r\n    // Unlike objects, no commas needed between methods\r\n    method1() {}\r\n    method2() {}\r\n}\r\n\r\nclass Animal {\r\n    constructor(name, isAwake) {\r\n        super();\r\n        this.name = name;\r\n        this.isAwake = isAwake;\r\n    }\r\n\r\n    wakeUp() {\r\n        this.isAwake = true;\r\n    }\r\n\r\n    sleep() {\r\n        this.isAwake = false;\r\n    }\r\n}"