export default "// `let` allows reassigning to the same variable\r\nlet a = 1;\r\na = 2;\r\n\r\n// `const` does not:\r\nconst b = 3;\r\nb = 4; // error!\r\n\r\n// However, `const` objects and arrays can be mutated!\r\nconst obj1 = {};\r\nobj1.c = 5;\r\n\r\nconst arr1 = [];\r\narr1[0] = 6;\r\narr1.push(7);"