export default "// Arrays can be used as queues, modifying the end:\r\nlet arr2 = [\"a\", \"b\"];\r\narr2.push(\"c\");\r\nconsole.log(arr2);\r\n// [\"a\", \"b\", \"c\"]\r\nlet c = arr2.pop();\r\n\r\n// Or as stacks, modifying the front:\r\narr2.unshift(\"d\");\r\nconsole.log(arr2);\r\n// [\"d\", \"a\", \"b\"]\r\nlet d = arr2.shift();\r\n\r\n// toString() returns a comma-separated string\r\nconsole.log(arr2.toString()); // \"a,b\""