export default "// Assume this tag exists in the page:\r\n<div id=\"container\" />\r\n\r\n// Later in the page:\r\n<script>\r\n  // Global `document` object represents the root of the page\r\n  // Nodes have APIs for querying descendents\r\n  let container = document.getElementById(\"container\");\r\n  container.innerHTML = \"New Content!\";\r\n\r\n  // Dynamically create new nodes\r\n  let div2 = document.createElement(\"div\");\r\n  div2.innerText = \"Hello!\";\r\n  div2.classList.append(\"test\");\r\n  container.appendChild(div2);\r\n\r\n  let testClassNodes = document.querySelectorAll(\"div.test\");\r\n  // Returns a NodeList, not an actual array, but close:\r\n  testClassNodes.forEach(node => console.log(node.innerText));\r\n\r\n  // Dynamically change styles (note \"camelCasing\" here!)\r\n  document.body.style.backgroundColor = \"red\";\r\n</script>"