import createTheme from "./createTheme";

/*
const colors = {
  primary: "white",
  secondary: "#1F2022", // almost black w/ blueish tint
  tertiary: "#fc300f", // bright red
  quaternary: "#CECECE" // pale gray
};
*/
const colors = {
    primary : "white",
    secondary : "#1F2022",
    tertiary : "#03A9FC",
    quaternary : "black"
}

const theme = createTheme(colors, {
    primary: "Montserrat",
    secondary: "Helvetica"
  }, {
    progress: {
      pacmanTop: {
        background: colors.quaternary
      },
      pacmanBottom: {
        background: colors.quaternary
      },
      point: {
        borderColor: colors.quaternary
      },
      number: {
        color : colors.quaternary
      }
    },
    components: {

      heading: {
        h1: {
          //fontSize: '4rem',
          //textTransform: 'uppercase',
          color : colors.quaternary
        },
        h2: {
          //fontSize: '3.5rem',
          //textTransform: 'uppercase',
            color : colors.quaternary
        },
        h3: {
          //fontSize: '3rem',
          //textTransform: 'uppercase',
            color : colors.quaternary
        },
        h4: {
          //fontSize: '2.5rem',
          //textTransform: 'uppercase',
            color : colors.quaternary
        },
        h5: {
          //fontSize: '2rem',
          //textTransform: 'uppercase',
            color : colors.quaternary
        },
        h6: {
          //fontSize: '1.5rem',
          //textTransform: 'uppercase',
            color : colors.quaternary
        }

      },

      codePane: {
        fontSize: '2rem'
      }
    }
  });

theme.screen.components.list.fontSize = "2.5rem";
delete theme.screen.components.listItem.fontSize;
theme.screen.components.codePane.fontSize = "1.5rem";
// Ignore the built-in dark theme and use the one we imported above
theme.screen.prism.dark = {};
//theme.screen.components.text.fontSize = "1.75rem";
delete theme.screen.components.list.padding;


export default theme;