export default "// Shorthand syntax for declaring key/value pairs:\r\nlet x = 0, y = 1;\r\n\r\nlet es5 = {x : x, y : y};\r\n// Create a key 'x' whose value is the variable 'x'\r\nlet es6 = {x, y};\r\n\r\n// Computed object property names:\r\nlet name = \"abc\";\r\n\r\nlet es5 = {};\r\nes5[name] = 123;\r\n\r\nlet es6 = {\r\n    // Use the value of variable 'name' as the property key\r\n    [name] : 123,\r\n    [name + \"2\"] : 456,\r\n}\r\n// {abc : 123, abc2 : 456}\r\n\r\n"