export default "// You can create pre-resolved or pre-rejected promises\r\nlet promise1 = Promise.resolve(42);\r\nlet promise2 = Promise.reject(\"PANIC!\");\r\n\r\n// Example of returning a promise:\r\nPromise.resolve(1)\r\n    .then( (first) => { // 1\r\n        return promise1;\r\n    })\r\n    .then( (second ) => { // 42\r\n        return promise2;\r\n    })\r\n    .catch( (error) => {\r\n        console.log(error) // \"PANIC!\"\r\n    })"