export default "// Properties can be set:\r\nmyElement.id = \"test-id\";\r\n\r\n// \"Classnames\" can be altered:\r\nmyElement.classList.add('foo')\r\nmyElement.classList.remove('bar')\r\nmyElement.classList.toggle('baz')\r\n\r\n// DOM nodes reference parents, children, and siblings:\r\nmyElement.children\r\nmyElement.firstChild\r\nmyElement.lastChild\r\nmyElement.previousSibling\r\nmyElement.nextSibling\r\nmyElement.parentNode\r\n\r\n// And can be moved around:\r\n// Append element1 as the last child of element2\r\nelement1.appendChild(element2)\r\n\r\n// Insert element2 as child of element 1, right before element3\r\nelement1.insertBefore(element2, element3)\r\n\r\nconst myElementClone = myElement.cloneNode()\r\nmyParentElement.appendChild(myElementClone)"