export default "// `let` and `const` are \"block scoped\", as you'd expect:\r\nfunction func2() {\r\n    console.log(a);\r\n\r\n    if(true) {\r\n        let a = 1;\r\n    } \r\n}\r\n\r\nfunc2() // Error: c is not defined\r\n\r\n// And you can \"shadow\" variables:\r\nfunction func3() {\r\n    let a = 1;\r\n\r\n    if(true) {\r\n        const a = 2;\r\n        console.log(a); // 2\r\n    }\r\n\r\n    console.log(a); // 1\r\n}"