export default "$demo.parent();           // accessing direct parent\r\n$(\"span\").parent().hide();     // changing parent color\r\n$demo.parents();          // all ancestors of the element\r\n$demo.children();         // all direct children\r\n$demo.children(\".first\"); // direct children w/ specified class\r\n$demo.find(\"span\");       // all span elements inside #demo\r\n\r\n\r\nlet $input = $(\"input.firstName\");\r\n$input.val(\"abcd\"); // set input value\r\nlet value = $input.val(); // get input value\r\n\r\n$input.examplePlugin(\"create\"); // initialize a plugin\r\n$input.on(\"customEvent\", () => {\r\n    console.log(\"Something happened\");\r\n});\r\n$input.examplePlugin(\"update\", 123);\r\n$input.examplePlugin(\"destroy\");"