export default "// For method calls, `this` points to the object in front\r\nlet person1 = {\r\n    name : \"Joe\",\r\n    sayHi() {\r\n        console.log(`My name is ${this.name}`)\r\n    }\r\n}\r\n\r\nperson1.sayHi(); // \"My name is Joe\"\r\n\r\nlet person2 = {\r\n    name: \"Sarah\",\r\n    sayHi : person1.sayHi,\r\n};\r\n\r\nperson2.sayHi(); // \"My name is Sarah\"\r\n\r\n\r\n// Function call, not a method call!\r\nconst {sayHi} = person1;\r\nsayHi(); // \"My name is undefined\""