export default "// `slice()` create a new array reference with the same contents\r\nlet arr1 = [\"a\", \"b\", \"c\", \"d\", \"e\"];\r\nlet arr2 = arr1.slice();\r\narr1 === arr2 // false\r\nconsole.log(arr2) // [\"a\", \"b\", \"c\", \"d\", \"e\"]\r\n\r\nlet arr3 = arr1.slice(-2) // negative indices!\r\nconsole.log(arr3); // [\"d\", \"e\"]\r\n\r\n// `concat()` creates a new array with more items at the end,\r\n// and flattens arrays passed as arguments\r\nlet arr4 = arr3.concat(\"f\", [\"g\"]);\r\nconsole.log(arr4); // [\"d\", \"e\", \"f\", \"g\"]\r\n\r\n// `join()`: converts values to strings with a separator\r\nlet str1 = arr1.join(\":\");\r\nconsole.log(str1); // \"1:3:6:7:12:17\""